import React, { useState, useEffect } from "react";
import { Input, Row, Col, Card, Form, TreeSelect, message, Button } from "antd";
import { LibraryCategoryApi } from "api/catalog-api";
import CkEditor from "views/app-views/shared/ckeditor";
import FileDialog from "views/app-views/shared/fileDialog";
import CatalogSelector from "views/app-views/shared/catalogSelector";
import { CatalogType } from "types/CatalogType";
import FileList from "views/app-views/shared/files";
import { CloudUploadOutlined, SendOutlined } from "@ant-design/icons";

const rules = {
  content: [
    {
      required: true,
      message: "Nhp vo ni dung",
    },
  ],
};

const GeneralField = (props) => {
  const [item, setItem] = useState(null);
  const [categories, setCategories] = useState(null);

  useEffect(() => {
    setItem(props.item);
    getCategories();
  }, []);

  const getCategories = () => {
    LibraryCategoryApi.getAll().then((response) => {
      setCategories(response.data);
    });
  };

  const changeCategoryId = (value) => {
    const currentItem = Object.assign({}, item);
    currentItem.categoryId = parseInt(value);
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange({ ...currentItem });
    }
  };

  const handleSelectChange = (field, value) => {
    const currentItem = Object.assign({}, item);
    currentItem[field] = value;
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange(currentItem);
    }
  };

  const handleFieldChange = (event) => {
    event.persist();
    const currentItem = Object.assign({}, item);
    const targetValue =
      event.target.type === "checkbox"
        ? event.target.checked
        : event.target.value;
    currentItem[event.target.name] = targetValue;
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange({ ...currentItem });
    }
  };

  const removeFile = (file) => {
    item.fileModels = item.fileModels.filter((x) => x.url != file.url);
    item["files"] = JSON.stringify(item.fileModels);
    setItem({ ...item });
    if (props.handleChange) {
      props.handleChange({ ...item });
    }
  };

  const selectFiles = () => {
    window.CKFinder.modal({
      chooseFiles: true,
      width: 800,
      height: 600,
      onInit: function (finder) {
        finder.on("files:choose", function (evt) {
          const currentItem = Object.assign({}, item);

          evt.data.files.map((file) => {
            const fileModel = {
              name: file.attributes.name,
              extension: file._extenstion,
              size: file.attributes.size,
              url: file.attributes.url,
            };
            currentItem.fileModels.push(fileModel);
          });

          currentItem["files"] = JSON.stringify(currentItem.fileModels);
          setItem({ ...currentItem });

          if (props.handleChange) {
            props.handleChange({ ...currentItem });
          }
        });
      },
    });
  };

  const selectAvatar = () => {
    window.CKFinder.modal({
      chooseFiles: true,
      width: 800,
      height: 600,
      onInit: function (finder) {
        finder.on("files:choose", function (evt) {
          const currentItem = Object.assign({}, item);
          var file = evt.data.files.first();
          currentItem["avatarModel"] = {
            name: file.attributes.name,
            extension: file._extenstion,
            size: file.attributes.size,
            url: file.attributes.url,
          };
          currentItem["AvatarUrl"] = JSON.stringify(currentItem["avatarModel"]);
          setItem({ ...currentItem });
          if (props.handleChange) {
            props.handleChange({ ...currentItem });
          }
        });
      },
    });
  };

  const hangeCatalogChange = (fieldName, value) => {
    const currentItem = Object.assign({}, item);
    currentItem[fieldName] = value;
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange({ ...currentItem });
    }
  };

  return (
    <>
      {item && (
        <Row gutter={16}>
          <Col xs={24} sm={24} md={17}>
            <Card title="Ni dung">
              <Form.Item label="Tiu ">
                <Input
                  name="name"
                  value={item.name}
                  onChange={handleFieldChange}
                />
              </Form.Item>
              <Row gutter={16}>
                <Col span={8}>
                  <Form.Item label="Khi lp">
                    <CatalogSelector
                      value={item.schoolBlockCatalogId}
                      onChange={(value) =>
                        hangeCatalogChange("schoolBlockCatalogId", value)
                      }
                      type={CatalogType.SCHOOL_BLOCK_CATALOG}
                    />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item label="Mn hc">
                    <CatalogSelector
                      value={item.subjectCatalogId}
                      onChange={(value) =>
                        hangeCatalogChange("subjectCatalogId", value)
                      }
                      type={CatalogType.SUBJECT_CATALOG}
                    />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item label="Trnh ">
                    {/* <Select
                      placeholder="Chn"
                      defaultValue={item.level}
                      value={item.level}
                      name="level"
                      onChange={(value) => handleSelectChange("level", value)}
                    >
                      <Option value={0}>Tt c</Option>
                      <Option value={1}>C bn</Option>
                      <Option value={2}>Trung cp</Option>
                      <Option value={3}>Cao cp</Option>
                    </Select> */}
                  </Form.Item>
                </Col>
              </Row>
              <Form.Item label="M t">
                <Input.TextArea
                  name="note"
                  value={item.note}
                  rows={4}
                  onChange={handleFieldChange}
                />
              </Form.Item>
              <Form.Item label="Ni dung">
                <CkEditor
                  data={item.content}
                  onChange={(data) => {
                    const currentItem = Object.assign({}, item);
                    currentItem.content = data;
                    setItem({ ...currentItem });
                    if (props.handleChange) {
                      props.handleChange({ ...currentItem });
                    }
                  }}
                />
              </Form.Item>
              <Form.Item label="File nh km">
                <Button
                  onClick={() => selectFiles()}
                  icon={<CloudUploadOutlined />}
                >
                  Chn file
                </Button>
                {item.fileModels && (
                  <FileList
                    removeFile={removeFile}
                    viewOnly={false}
                    files={item.fileModels}
                  />
                )}
              </Form.Item>
            </Card>
          </Col>
          <Col xs={24} sm={24} md={7}>
            <Card title="nh i din">
              <FileDialog
                onClick={() => selectAvatar()}
                fileUrlObject={item.avatarModel}
              />
            </Card>
            <Card title="Phn loi">
              <Form.Item name="category" label="Danh mc">
                {categories && (
                  <TreeSelect
                    style={{ width: "100%" }}
                    treeData={categories}
                    className="w-100"
                    dropdownStyle={{ maxHeight: 400, overflow: "auto" }}
                    placeholder="Chn Danh mc"
                    allowClear
                    defaultValue={[props.item.categoryName]}
                    treeDefaultExpandAll
                    onChange={(value) => changeCategoryId(value)}
                  ></TreeSelect>
                )}
              </Form.Item>
            </Card>
          </Col>
        </Row>
      )}
    </>
  );
};

export default GeneralField;
